@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
.search {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  place-items: center;
  width: 100%;
  max-width: 1300px;
  margin: auto;
  margin-top: 30px;
}

.search-field {
  width: 100%;
  height: 40px;
  flex: 2 1;
  display: flex;
  background-color: var(--lightYellow);
  border-radius: 5px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.search-icon {
  position: absolute;
  width: 15px;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}
.search--input {
  width: 100%;
  padding: 10px 40px;
  margin-right: 5px;
  background-color: var(--lightYellow);
  border: transparent;
}
::-webkit-input-placeholder {
  text-transform: capitalize;
}
:-ms-input-placeholder {
  text-transform: capitalize;
}
::-ms-input-placeholder {
  text-transform: capitalize;
}
::placeholder {
  text-transform: capitalize;
}
.search--button {
  text-transform: capitalize;
  background-color: var(--darkYellow);
  border: none;
  border-radius: 15px;
  padding: 0 20px;
  height: 30px;
  margin-right: 10px;
  cursor: pointer;
  font-weight: 700;
  color: var(--blue);
}
.search--button:hover {
  background-color: var(--yellow);
}
.search--requests {
  flex: 1 1;
  margin: 0;
  padding: 1rem;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  font-size: 1.5rem;
  color: var(--blue);
}

.user-info {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  width: 100%;
  max-width: 1000px;
  margin: 39px auto 100px auto;
}

.item {
  background-color: var(--lightYellow);
  display: flex;
  padding: 0px 25px;
}
.item-image {
  flex: 1 1;
  max-width: 35px;
  margin-right: 20px;
}
.item-data {
  flex: 3 1;
}
.data-value {
  font-size: 20px;
  font-weight: 700;
  margin: 10px 0;
}
.data-label {
  text-transform: capitalize;
  margin: 10px 0;
}

.user-section {
  position: relative;
  background-color: var(--lightYellow);
}
.user-header {
  width: 100%;
  padding-top: 30px;
  padding: 30px 10px 10px 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.header-left {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.header-left img {
  border-radius: 100%;
  margin-right: 10px;
  width: 70px;
  height: 70px;
}

.header-left div {
}
.header-left div h4 {
  word-wrap: break-word;
  text-transform: capitalize;
  margin-bottom: 7px;
}
.header-left div p {
  margin-top: 0px;
  color: var(--text);
}

.header-right {
  margin-left: 10px;
  text-decoration: none;
  text-transform: capitalize;
  background-color: var(--darkYellow);
  color: var(--blue);
  font-weight: 700;
  padding: 8px 15px;
  border-radius: 20px;
  margin-top: 15px;
}
.header-right:hover {
  background-color: var(--yellow);
}
.user-data {
  padding: 10px;
}

.description {
  margin: 20px 0 40px 0;
  color: var(--text);
}
.links {
}
.links p {
  display: flex;
  align-items: center;
  color: var(--lightBlue);
}
.links p img {
  width: 25px;
  margin-right: 10px;
}

.links p a {
  color: var(--lightBlue);

  display: inline-block;
  width: 80%;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

.links p a:hover {
  color: var(--darkBlue);
}

.followers-section {
  position: relative;
  background-color: var(--lightYellow);
}

.followers-container {
  width: 100%;
  height: 92%;
  position: absolute;
  overflow-y: scroll;
  top: 20px;
  left: 0;
  padding: 10px 25px;
  padding-top: 25px;
}
.follower {
  display: flex;
  margin: 10px 0;
  width: 100%;
}

.follower img {
  width: 50px;
  border-radius: 100%;
  margin-right: 10px;
}

.follower h4 {
  margin: 0;
  text-transform: capitalize;
}

.follower a {
  color: var(--lightBlue);
}

.follower a:hover {
  color: var(--darkBlue);
}

.popular-repos-section {
  position: relative;
  background-color: var(--lightYellow);
  padding: 10px 25px;
}

.languages-section {
  position: relative;
  background-color: var(--lightYellow);
  padding: 10px 25px;
}

.chart-centred {
  height: 100%;
  display: grid;
  place-items: center;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: auto;
}
.spinner {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #3498db;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.error-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 111111;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(14, 14, 14, 0.4);
}
.error-message {
  width: 100%;
  max-width: 500px;
  background-color: #1d78be;
  padding: 30px 50px;
  border-radius: 10px;
  color: white;
}

.inside {
  position: relative;
}
.close {
  position: absolute;
  top: 20px;
  right: 10px;
  width: 40px;
  height: 10px;
  cursor: pointer;
}
.bar {
  width: 100%;
  height: 5px;
  background-color: red;
  position: absolute;
  border-radius: 20px;
}
.bar:first-child {
  transform: rotate(45deg);
}
.bar:nth-child(2) {
  transform: rotate(-45deg);
}

:root {
  --blue: #1a659e;
  --lightBlue: #1d78be;
  --darkBlue: #093d64;
  --text: rgba(14, 14, 14, 0.9);
  --pink: #eeabc4;
  --lightPink: #f8c6d9;
  --green: #588157;
  --yellow: #efefd0;
  --lightYellow: #f5f5e5;
  --darkYellow: #ebebac;
  --blackYellow: #cfcf99;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #efefd0;
  background-color: var(--yellow);
  min-height: 100vh;
  width: 90%;
  margin: auto;
}

.grid-layout {
  color: #4e66c5;
  display: grid;
  padding-top: 50px;
  margin: auto;
  grid-auto-rows: minmax(400px, auto);
  grid-template-columns: 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 70px;
  padding-bottom: 50px;
}
@media all and (min-width: 1000px) {
  .grid-layout {
    grid-template-columns: 1fr 1fr;
  }
}

.section-title {
  position: absolute;
  top: -53px;
  left: 0;
  background-color: #f5f5e5;
  background-color: var(--lightYellow);
  padding: 10px 20px;
  text-transform: capitalize;
  color: #1a659e;
  color: var(--blue);
  z-index: 10;
}

.error-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.h1-404 {
  font-size: 100px;
}
.error-message {
  text-align: center;
  font-size: 25px;
}
.error-message::first-letter {
  text-transform: uppercase;
}
.back-home-btn {
  background-color: #cfcf99;
  background-color: var(--blackYellow);
  padding: 10px 30px;
  border-radius: 10px;
  color: black;
  border: none;
  box-shadow: 0 10px 5px -6px #777;
  cursor: pointer;
  font-size: 20px;
  -webkit-animation: bounce 1s infinite;
          animation: bounce 1s infinite;
  text-decoration: none;
}
.back-home-btn:hover {
  background-color: #ebebac;
  background-color: var(--darkYellow);
}
@-webkit-keyframes bounce {
  50% {
    border-radius: 20px;
  }
}
@keyframes bounce {
  50% {
    border-radius: 20px;
  }
}

